'use client';

import { useState, useEffect, useRef } from 'react';
import { X, Settings, RotateCcw } from 'lucide-react';
import { videoApi } from '@/lib/api/video-api';
import type { VideoSource } from '@/lib/api/video-api';
import { useRouter } from 'next/navigation';

interface VideoPlayerProps {
  mediaId: number;
  mediaType: 'movie' | 'tv';
  title?: string;
  season?: number;
  episode?: number;
  onClose?: () => void;
}

export default function VideoPlayer({
  mediaId,
  mediaType,
  title,
  season,
  episode,
  onClose
}: VideoPlayerProps) {
  const [movie, setMovie] = useState<any>(null);
  const [tvShow, setTvShow] = useState<any>(null);
  const [embedUrl, setEmbedUrl] = useState<string>('');
  const [sources, setSources] = useState<VideoSource[]>([]);
  const [currentSourceIndex, setCurrentSourceIndex] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showControls, setShowControls] = useState(true);
  const [controlsTimeout, setControlsTimeout] = useState<NodeJS.Timeout | null>(null);
  const [showSourceSelector, setShowSourceSelector] = useState(false);
  
  const iframeRef = useRef<HTMLIFrameElement>(null);
  const router = useRouter();

  // Load video data
  useEffect(() => {
    let cancelled = false;
    (async () => {
      setLoading(true);
      setError(null);
      try {
        if (mediaType === 'movie') {
          const { movie: m, embedUrl: url, sources: srcs } =
            await videoApi.getMovieWithEmbed(mediaId);
          if (cancelled) return;
          setMovie(m);
          setSources(srcs);
          setEmbedUrl(url);
        } else {
          const { tvShow: tv, embedUrl: url, sources: srcs } =
            await videoApi.getTVShowWithEmbed(mediaId, season, episode);
          if (cancelled) return;
          setTvShow(tv);
          setSources(srcs);
          setEmbedUrl(url);
        }
        setCurrentSourceIndex(0);
      } catch (e) {
        if (!cancelled) setError('Failed to load video. Please try again.');
      } finally {
        if (!cancelled) setLoading(false);
      }
    })();
    return () => { cancelled = true; };
  }, [mediaId, mediaType, season, episode]);

  // Update embed URL when source changes
  useEffect(() => {
    const s = sources[currentSourceIndex];
    if (s) {
      setEmbedUrl(s.url);
      setError(null);
    }
  }, [currentSourceIndex, sources]);

  // Auto-hide controls
  useEffect(() => {
    if (showControls) {
      if (controlsTimeout) clearTimeout(controlsTimeout);
      const timeout = setTimeout(() => {
        setShowControls(false);
      }, 3000);
      setControlsTimeout(timeout);
    }
    return () => {
      if (controlsTimeout) clearTimeout(controlsTimeout);
    };
  }, [showControls]);

  // Show controls on mouse movement
  const handleMouseMove = () => {
    setShowControls(true);
  };

  const handleIframeError = () => {
    if (currentSourceIndex < sources.length - 1) {
      setCurrentSourceIndex(i => i + 1);
    } else {
      setError('All video sources failed to load.');
    }
  };

  const switchSource = (i: number) => {
    if (i >= 0 && i < sources.length) {
      setCurrentSourceIndex(i);
      setShowSourceSelector(false);
    }
  };

  const handleClose = () => {
    if (onClose) {
      onClose();
    } else {
      router.back();
    }
  };

  const mediaTitle = title || movie?.title || tvShow?.name || 'Unknown';
  const currentSource = sources[currentSourceIndex];

  return (
    <div 
      className="fixed inset-0 z-50 bg-black"
      onMouseMove={handleMouseMove}
      onMouseLeave={() => setShowControls(true)}
    >
      {/* Top Controls - Auto-hide */}
      <div className={`absolute top-0 left-0 right-0 z-20 transition-opacity duration-300 ${
        showControls ? 'opacity-100' : 'opacity-0'
      }`}>
        <div className="flex items-center justify-between p-4 bg-gradient-to-b from-black/80 to-transparent">
          {/* Title Info */}
          <div className="flex-1">
            <h1 className="text-white text-lg font-semibold truncate">{mediaTitle}</h1>
            {mediaType === 'tv' && season && episode && (
              <p className="text-gray-300 text-sm">
                Season {season} • Episode {episode}
              </p>
            )}
          </div>

          {/* Top Right Controls */}
          <div className="flex items-center gap-2">
            {/* Source Selector */}
            {sources.length > 1 && (
              <div className="relative">
                <button
                  onClick={() => setShowSourceSelector(!showSourceSelector)}
                  className="p-2 rounded-full bg-black/50 hover:bg-black/70 text-white transition-colors"
                  aria-label="Video sources"
                >
                  <Settings className="w-5 h-5" />
                </button>

                {/* Source Dropdown */}
                {showSourceSelector && (
                  <div className="absolute top-12 right-0 bg-black/90 backdrop-blur-sm rounded-lg p-3 min-w-48">
                    <h3 className="text-white text-sm font-medium mb-2">Video Source</h3>
                    <div className="space-y-1">
                      {sources.map((s, i) => (
                        <button
                          key={`${s.servers}-${i}`}
                          onClick={() => switchSource(i)}
                          className={`w-full text-left px-3 py-2 rounded text-sm transition-colors ${
                            i === currentSourceIndex
                              ? 'bg-blue-600 text-white'
                              : 'text-gray-300 hover:bg-gray-700'
                          }`}
                        >
                          <div className="font-medium">{s.servers}</div>
                          <div className="text-xs opacity-75">{s.quality}</div>
                        </button>
                      ))}
                    </div>
                  </div>
                )}
              </div>
            )}

            {/* Close Button */}
            <button
              onClick={handleClose}
              className="p-2 rounded-full bg-black/50 hover:bg-black/70 text-white transition-colors"
              aria-label="Close player"
            >
              <X className="w-5 h-5" />
            </button>
          </div>
        </div>
      </div>

      {/* Video Container */}
      <div className="relative w-full h-full flex items-center justify-center">
        {/* Loading State */}
        {loading && (
          <div className="flex flex-col items-center gap-4 text-white">
            <div className="animate-spin rounded-full h-12 w-12 border-2 border-white border-t-transparent" />
            <p className="text-sm">Loading video...</p>
          </div>
        )}

        {/* Error State */}
        {error && !loading && (
          <div className="flex flex-col items-center gap-4 text-center max-w-sm text-white px-4">
            <div className="text-red-500 text-4xl">⚠️</div>
            <div>
              <h3 className="text-lg font-semibold mb-2">Video Unavailable</h3>
              <p className="text-gray-400 text-sm mb-4">{error}</p>
              <div className="flex gap-3 justify-center">
                {currentSourceIndex < sources.length - 1 && (
                  <button
                    onClick={() => switchSource(currentSourceIndex + 1)}
                    className="px-4 py-2 bg-blue-600 hover:bg-blue-700 rounded text-sm transition-colors"
                  >
                    Try Next Source
                  </button>
                )}
                <button
                  onClick={() => location.reload()}
                  className="px-4 py-2 bg-gray-600 hover:bg-gray-700 rounded text-sm transition-colors"
                >
                  <RotateCcw className="w-4 h-4 inline mr-1" />
                  Reload
                </button>
              </div>
            </div>
          </div>
        )}

        {/* Video Player - Full Screen */}
        {embedUrl && !loading && !error && (
          <iframe
            ref={iframeRef}
            src={embedUrl}
            title={`Watch ${mediaTitle}`}
            className="w-full h-full"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            allowFullScreen
            onError={handleIframeError}
            style={{ border: 'none' }}
          />
        )}
      </div>

      {/* Bottom Source Info - Auto-hide */}
      {currentSource && sources.length > 1 && (
        <div className={`absolute bottom-4 left-4 z-20 transition-opacity duration-300 ${
          showControls ? 'opacity-100' : 'opacity-0'
        }`}>
          <div className="bg-black/70 backdrop-blur-sm rounded-lg px-3 py-2">
            <p className="text-white text-xs">
              {currentSource.servers} • {currentSourceIndex + 1}/{sources.length}
            </p>
          </div>
        </div>
      )}
    </div>
  );
}
